.page-header {
  position: relative;
  background-color: $dark-two;
}

.page-header__wrapper {
  @include container;
  position: relative;
  min-height: 73px;
  display: flex;
  justify-content: space-between;
  padding-top: 27px;
  padding-bottom: 27px;
}

.page-header__menu-open {
  position: absolute;
  display: block;
  padding: 0;
  border: none;
  background-color: transparent;
  cursor: pointer;

  &::before,
  &::after {
    content: "";
    position: absolute;
    left: 245px;
    width: 25px;
    height: 3px;
    border-radius: 3px;
    background-color: $white;
  }

  &::before {
    top: 5px;
  }

  &::after {
    top: 12px;
  }
}

